.error-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  text-align: center;
  i {
    display: block;
    margin: 1rem auto 0;
    width: 2.12rem;
    height: 1.48rem;
    background: url(data:image/jpg;base64,) center / 100% 100%;
  }
  p {
    margin-top: 0.44rem;
    font-size: 0.36rem;
  }
  a {
    display: block;
    width: 50%;
    margin: 1.2rem auto 0 ;
    line-height: 0.88rem;
    border-radius: 0.12rem;
    border: 1px solid #f64;
    color: #f64;
    text-decoration: none;
  }
}